.buttonStyle{
    color:white;
    background: rgb(146, 29, 101);
    padding: 2% 1%;
    margin: 3% 0 2%;
    outline: none;
    border: none;
}
.buttonStyle:hover{
    background: rgb(199, 38, 137);
    cursor: pointer;
}
@media screen and (max-width: 768px){
    .buttonStyle{
        margin-top: 10%;
        margin-bottom: 0;
        padding: 5%;
        border-radius: 50px;
    }
}